.jumbotron {
    margin: 5% 0% 0% 0%;
}

@media only screen and (max-width: 600px) {
    .jumbotron {
        margin: 25% 0% 0% 0%;
    }
}
.nav {
    background: #ae5ee0;
    width: 95%;
    position: fixed;
    margin-left: 3%
}

.nav-item {
    display: -webkit-flex;
    display: flex;
    margin: 0% 5% 0% 5%;
    padding: 0.5%;
}
  
.nav a {
    color: white;
    font-size: 1.3rem;
    text-decoration: none;
    margin: 5px;
    line-height: 1;
}
.card {
    background: #ae5ee0;
    width: 175px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    display: inline-block;
    margin: 2% 3% 2% 3%;
    position: static;
}
  
.card > .img-container {
    height: 80%;
    background:#6CADDC;
}

.card > .img-container > img {
    width: 100%;
}

.char-name {
    text-align: center;
    font-size: 75%;
}


@media only screen and (max-width: 600px) {
    .card {
        width: 150px;
    }
}
.nava {
    background: #ae5ee0;
    width: 95%;
    position: absolute;
    margin-left: 3%
}

.nava-item {
    display: -webkit-flex;
    display: flex;
    margin: 0% 5% 0% 5%;
    padding: 0.5%;
}
  
.nava a {
    color: white;
    font-size: 1.3rem;
    text-decoration: none;
    margin: 5px;
    line-height: 1;
}
